import {createEnhancedElement} from '@servicenow/library-enhanced-element';
import {
	buildAndSanitizePropsMaps,
	createXenoDomReadyHandler,
	applyPropToChartInstance,
	createXenoPropPreProcessMap
} from '@servicenow/library-chart';
import themeBehavior from '@servicenow/behavior-theme';
import {Sparkline} from 'xenolith/dist/es';
import styles from './_now-chart-sparkline.scss';
import {
	axesConfigSchema,
	seriesConfigSchema,
	thresholdConfigSchema,
	thresholdDataPointSchema,
	targetSeriesConfigSchema
} from './schemas/chart-sparkline-schema';
import {actionTypes} from '@servicenow/ui-core';
import {configAriaSchema} from '@servicenow/library-schemas';
const {
	COMPONENT_DOM_READY,
	COMPONENT_PROPERTY_CHANGED,
	COMPONENT_DISCONNECTED
} = actionTypes;

/*
 * TODO: This is a temporary way of debugging customer problems during the
 * Seismic 18 upgrade process, because tectonic resolution makes it hard to
 * know what version of a component is running. Remove when not needed.
 * Intentionally runs in dev + production environments so we can debug all builds.
 */
const {name, version} = require('../package.json');
const registry = (window.___NOW_DESIGN_SYSTEM_PACKAGES___ =
	window.___NOW_DESIGN_SYSTEM_PACKAGES___ || {});
if (!registry[name]) {
	registry[name] = version;
}

/** @seismicView now-chart-sparkline */
const view = (state) => {
	/*
    Must 1) wait for the chartInstance / behavior to be instatiated
    2) only apply the theme if it is different or else it triggers a Xenolith render.
  */
	if (
		state.chartInstance &&
		state.behaviors.theme &&
		JSON.stringify(state.behaviors.theme) !==
			JSON.stringify(state.chartInstance.theme)
	) {
		state.chartInstance.theme = state.behaviors.theme;
	}
	return <div id="chart-container" />;
};

const PROPERTIES = {
	/**
	 * Specifies whether the chart is rendered in accessibility mode.
	 * @type {boolean}
	 */
	accessibilityMode: {
		xenolith: true,
		default: false,
		schema: {type: 'boolean'}
	},
	/**
	 * An object whose keys reference a specific HTML element within the
	 * now-chart-sparkline. The value of each key is an object that contains
	 * an ARIA property (or properties) and corresponding value/s. These ARIA
	 * properties will be set on the specified element.
	 * The 'chart' key corresponds to the inner html `<div>` element in
	 * now-chart-sparkline that wraps the chart and the register. Use this property to
	 * supply an aria-label that describes the chart for screen reader users. See
	 * https://www.w3.org/TR/wai-aria-1.1/ for other properties and accepted values.
	 * @type {{ 'aria-*': string }}
	 */
	configAria: {
		schema: {
			anyOf: [
				{
					type: 'object',
					properties: {
						chart: configAriaSchema
					},
					additionalProperties: false
				},
				configAriaSchema
			]
		}
	},
	/**
	 * Object containing the configuration of the x and y axes.
	 * @type {{color: string, strokeWidth: number}}
	 */
	axesConfig: {xenolith: true, schema: axesConfigSchema},
	/**
	 * Fixed or dynamic extents, limiting the data values displayed on the chart to a specific range.
	 * To specify an extent, pass an object whose keys are the axis names and whose values are array
	 * tuples holding `[min, max]` values. By default, the range is calculated dynamically from the data.
	 *
	 * For example:
	 * ```js
	 * extents: {
	 *   x: ['dynamic', 10000],
	 *   y: [0, 100]
	 * }
	 * ```
	 *
	 * @type {Object<string, Array<number | 'dynamic'>>}
	 */
	extents: {
		xenolith: true,
		schema: {
			type: 'object',
			patternProperties: {
				'.*': {
					type: 'array',
					items: {oneOf: [{type: 'number'}, {const: 'dynamic'}]},
					minItems: 2,
					maxItems: 2
				}
			}
		}
	},
	/**
	 * The chart's height in pixels. Enter either the number of pixels, or the string `auto`
	 * to use the height of the parent DOM element.
	 * @type {string | number}
	 */
	height: {
		xenolith: true,
		schema: {
			oneOf: [{const: 'auto'}, {type: 'number'}]
		}
	},
	/**
	 * The main configuration for the chart. This property is a map object containing
	 * the chart name and seriesConfig object.
	 *
	 * For example:
	 * ```js
	 * seriesConfig: {
	 * 	myChartName: { x: 'timestamp', y: 'amount', type: 'area' }
	 * }
	 * ```
	 *
	 * @type {Object<string, SeriesConfig>}
	 */
	seriesConfig: {
		xenolith: true,
		schema: {
			type: 'object',
			patternProperties: {'.*': seriesConfigSchema}
		}
	},
	/**
	 * The array of data points that define the line on the chart.
	 * Works in conjunction with the `seriesConfig` property.
	 * For example, if `seriesConfig` defines the x key as `timestamp`
	 * and the y key as `amount`, a series of data points on the chart might be:
	 * ```js
	 * seriesData: [
	 * 	   {timestamp: 1226766192448, amount: 5},
	 * 	   {timestamp: 1226766192450, amount: 7}
	 * ]
	 * ```
	 *
	 * @type {SeriesDataPoint[]}
	 */
	seriesData: {xenolith: true, schema: {type: 'array'}},
	/**
	 * The target element on the chart, rendered as a horizontal line
	 * with multiple sequential values for each target series.
	 * Only include one target series per sparkline chart.
	 * This property is a map object containing
	 * the target name and TargetSeriesConfig object,
	 * where the TargetSeriesConfig properties include:
	 * - `x`: String. Name of the x key used in the data points in targetData.
	 * - `y`: String. Name of the y key used in the data points in targetData.
	 * - `strokeWidth`: Number. Represents the thickness of the target line in pixels.
	 * - `color`: String. Color of the target line.
	 * @type {Object<string, TargetSeriesConfig>}
	 */
	targetsConfig: {
		xenolith: true,
		schema: {
			type: 'object',
			patternProperties: {'.*': targetSeriesConfigSchema}
		}
	},
	/**
	 * When true, displays the x and y axes.
	 * @type {boolean}
	 */
	showAxes: {
		xenolith: true,
		default: false,
		schema: {type: 'boolean'}
	},
	/**
	 * The array of data points defining the target line on the chart.
	 * Works in conjunction with the `targetsConfig` property.
	 * For example, if `targetConfig` defines the x key as `x`
	 * and the y key as `y`, a series of target data points on the chart might be:
	 * ```js
	 * targetsData: [
	 * 	{ x: 0, y: 1.5 },
	 * 	{ x: 15, y: 0.5 },
	 * 	{ x: 30, y: 1 }
	 * ]
	 * ```
	 *
	 * @type {SeriesDataPoint[]}
	 */
	targetsData: {xenolith: true, schema: {type: 'array'}},
	/**
	 * Optional.
	 * The threshold element on the chart rendered as a horizontal line.
	 * This property is a map object containing the threshold name and the
	 * configuration for the threshold, where the threshold configuration properties include:
	 * - `strokeWidth`: Number. Represents the thickness of the threshold line in pixels.
	 * - `color`: String. Color of the threshold line.
	 * @type {Object<string, ThresholdConfig>}
	 */
	thresholdConfig: {
		xenolith: true,
		schema: {
			type: 'object',
			patternProperties: {'.*': thresholdConfigSchema}
		}
	},
	/**
	 * The array containing a single data point that define the threshold element on the chart.
	 * Only include one threshold line per sparkline chart.
	 * Works in conjunction with the `thresholdConfig` property.
	 * Link the threshold value with a specific threshold config using the keyword 'type'.
	 *
	 * For example:
	 * ```js
	 * thresholdData: [{ value: 1.1, type: 'yourThresholdName' }]
	 * ```
	 *
	 * @type {ThresholdData[]}
	 */
	thresholdData: {
		xenolith: true,
		schema: {
			type: 'array',
			items: thresholdDataPointSchema
		}
	},
	/**
	 * The chart's width in pixels. Enter either the number of pixels, or the string `auto`
	 * to use the width of the parent DOM element.
	 * @type {string | number}
	 */
	width: {
		xenolith: true,
		schema: {
			oneOf: [{const: 'auto'}, {type: 'number'}]
		}
	}
};

const {XENOLITH_PROPERTIES} = buildAndSanitizePropsMaps(PROPERTIES);
const xenoPropPreProcessMap = createXenoPropPreProcessMap();

/**
 * A sparkline chart displays the general shape or variation of a measurement, typically over time.
 * For example, you can use a sparkline chart to show temperature or stock market price variation,
 * in a simple and condensed way.
 *
 * ```jsx
 * const options = {
 *   width: "auto",
 *   height: "auto",
 *   seriesConfig: {
 *     foo: { x: "timestamp", y: "y0", type: ["area"]}
 *   },
 *   seriesData: [
 * 	   {timestamp: 1226766192448, y0: 5},
 *     ...otherData
 *   ]
 * }
 * <now-chart-sparkline {...options} />
 * ```
 *
 * @seismicElement now-chart-sparkline
 * @summary Small and simple data visualizations that measure values over time in the form of a line or area chart.
 */
createEnhancedElement('now-chart-sparkline', {
	properties: PROPERTIES,
	view,
	styles,
	behaviors: [
		{
			behavior: themeBehavior,
			options: {
				items: ['type.fontFamily', 'global.*', 'datavis.*']
			}
		}
	],
	actionHandlers: {
		[COMPONENT_DOM_READY]: createXenoDomReadyHandler(
			{
				xenoPropList: XENOLITH_PROPERTIES,
				preProcessMap: xenoPropPreProcessMap
			},
			({host, state}) => {
				const chartContainer = host.shadowRoot.getElementById(
					'chart-container'
				);

				return new Sparkline({
					node: chartContainer,
					theme: state.behaviors.theme
				});
			}
		),
		[COMPONENT_PROPERTY_CHANGED]: ({
			action,
			state,
			dispatch,
			updateState,
			host,
			properties
		}) => {
			const {name, value, previousValue} = action.payload;

			applyPropToChartInstance({
				chartInstance: state.chartInstance,
				value,
				previousValue,
				name,
				xenoPropList: XENOLITH_PROPERTIES,
				dispatch,
				preProcessMap: xenoPropPreProcessMap,
				state,
				updateState,
				host,
				properties
			});
		},
		[COMPONENT_DISCONNECTED]: ({state}) => {
			state.chartInstance.destroy();
		}
	}
});
